.review {
  background-color: $color-background-blue;
  padding: 55px 62px;
  color: $color-default-white;
  width: 560px;
  display: grid;
  grid-template-columns: 80px 1fr;
  column-gap: 28px;

  div {
    display: flex;
    flex-direction: column;
  }

  h2 {
    font-size: 20px;
    font-weight: 700;
    line-height: 30px;
    margin: 0;
  }

  p {
    margin: 0;
    margin-top: 12px;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
  }

  img {
    margin-top: 6px;
    border-radius: 50%;
  }

  @include vp-1199 {
    width: 565px;
    padding: 53px 63px 58px;
    column-gap: 30px;

    img {
      margin-top: 8px;
    }
  }

  @include vp-767 {
    width: 100%;
  }
}
