.swiper {
  width: 85%;
  max-width: 1160px;

  @include vp-1199 {
    width: 74%;
  }

  @include vp-767 {
    width: 71%;
  }
}

.swiper-container {
  position: relative;
  max-width: 1366px;
  margin: 0 auto;
}

