.swiper-review-container {
  position: relative;
  margin: 0 auto;
  max-width: 1160px;
  overflow: hidden;

  @include vp-767 {
    width: 70%;
  }
}

.swiper-button-prev {
  background-image: url("../../img/svg/button-prev.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 16px 32px;
  margin-left: 19.5%;

  &::after {
    display: none;
  }

  @include vp-1199 {
    margin-left: 4%;
  }

  @include vp-767 {
    background-size: 10px 19px;
    margin-left: 40px;
  }
}

.swiper-button-next {
  background-image: url("../../img/svg/button-next.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 16px 32px;
  margin-right: 19.5%;

  &::after {
    display: none;
  }

  @include vp-1199 {
    margin-right: 4%;
  }

  @include vp-767 {
    background-size: 10px 19px;
    margin-right: -3px;
  }
}
